import ThreeCol from '../components/ThreeColLayout'
import useAuth from "../hooks/userAuth";
const { useEffect } = React;


const myTheme = createTheme({

})
const IndexPage = () => {
    const { isAuth, env } = useAuth()

    const [value, setValue] = React.useState(
        dayjs('1988-09-14T12:00:01'),
    );
    const [post, setPosts] = useState([])
    const [preview, setPreview] = useState('')
    const [number, setNumber] = useState('')
    const [day, setDate] = useState('')
    const [month, setMonth] = useState('')
    const [year, setYear] = useState('')
    const [elements, setElements] = useState(null)
    const [accordion, setAccordion] = useState([])
    const theme = useTheme();
    const [numberInfo, setNumberInfo] = useState()
    const matches = useMediaQuery(theme.breakpoints.up('sm'));
    const handleChange = (newValue) => {
        console.log(newValue)
        setValue(newValue);
    };


    const onNumber = (result) => {
        setNumberInfo(result)
    }

    const refreshData = async () => {
        const result = await Parse.Cloud.run('getPost', {
            category: `public-${env}`,
        })
        setPosts(result?.map((e) => e.toJSON()) || [])
    }

    useEffect(async () => {
        refreshData()
    }, [])



    var refdiv = React.useRef(null);
    var rte;

    const handleDelete = async (post) => {
        await Parse.Cloud.run('deleteItems', {
            ids: [post.objectId]
        })
        refreshData()
    }

    const handlePost = async (post) => {
        await Parse.Cloud.run('post', {
            title: post.title,
            content: post.content,
            category: `public-${env}`,
            ...post,
        })
        refreshData()
    }

    return (
        <>
            <ThreeCol env={env}>
                <Seo title="Home" />
                <h3>OpenFengShui - Numbers And TCM</h3>
                <Box display="flex" justifyContent="center" flexDirection="column" gap="16px">

                    <PostList list={post} editMode={isAuth()} handleDelete={handleDelete} onPost={handlePost} />
                </Box>
            </ThreeCol>
        </>)
}

export default IndexPage
